import { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import ngeohash from 'ngeohash';
import CardProduto from './componentes/CardProduto';
import ModalEndereco from './componentes/ModalEndereco';
import usePersistedState from './hooks/usePersistedState';

import './App.css';

const TAMANHO_MAXIMO = 150;

const App = () => {
    const [periodo, setPeriodo] = useState("3");
    const [ordem, setOrdem] = useState("0");
    const [pesquisarPor, setPesquisarPor] = useState("NOME");
    const [termo, setTermo] = useState("");
    const [categoria, setCategoria] = useState("");
    const [distancia, setDistancia] = useState("20");
    const [categorias, setCategorias] = useState([]);
    const [produtos, setProdutos] = useState([]);
    const [loading, setLoading] = useState(false);
    const [fazerPesquisa, setFazerPesquisa] = useState(false);
    const [modalVisivel, setModalVisivel] = useState(false);
    const [endereco, setEndereco] = usePersistedState("endereco", null);
    const refTermo = useRef(null);

    useEffect(() => {
        if (refTermo.current)
            refTermo.current.focus();
        if (!endereco)
            setModalVisivel(true);
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        if (fazerPesquisa) {
            pesquisar();
            setFazerPesquisa(false);
        }
        // eslint-disable-next-line
    }, [fazerPesquisa]);

    const buscarCategorias = async () => {
        try {
            const response = await axios.get(
                `https://menorpreco.notaparana.pr.gov.br/api/v1/categorias` +
                `?local=${endereco.geohash}` +
                `&termo=${termo}` +
                `&raio=${distancia}`
            );
            const categoriasRetornadas = response.data.categorias;
            console.log('Categorias retornadas:', categoriasRetornadas);

            if (categoriasRetornadas.length === 0)
                return "";

            setCategorias(categoriasRetornadas);
            setCategoria(categoriasRetornadas[0].id);
            return categoriasRetornadas[0].id;
        } catch (error) {
            console.error('Erro ao buscar categorias:', error);
        }
    }

    const pesquisar = async () => {
        if (!termo) return; // No faz a busca se o termo estiver vazio
        let todosProdutos = [];
        let offset = 0;
        let continuarBuscando = true;
        let categoriaAtual = categoria;
        let campoPesquisa = pesquisarPor === "NOME" ? "termo" : "gtin";

        try {
            setLoading(true);

            if (pesquisarPor === "NOME" && categorias.length === 0)
                categoriaAtual = await buscarCategorias();
            console.log('Categoria atual:', categoriaAtual);

            while (continuarBuscando && offset < TAMANHO_MAXIMO) {
                const response = await axios.get(
                    `https://menorpreco.notaparana.pr.gov.br/api/v1/produtos` +
                    `?local=${endereco.geohash}` +
                    `&ordem=${ordem}` +
                    `&data=${periodo}` +
                    `&${campoPesquisa}=${termo}` +
                    `&offset=${offset}` +
                    `&raio=${distancia}` +
                    (pesquisarPor === "NOME" ? `&categoria=${categoriaAtual}` : "")
                );

                const produtosRetornados = response.data.produtos;

                if (produtosRetornados.length > 0) {
                    todosProdutos = [...todosProdutos, ...produtosRetornados];
                    offset += 50; // Incrementa o offset para a prxima pgina
                } else {
                    continuarBuscando = false; // Para a busca se a lista de produtos estiver vazia
                }
            }
            setProdutos(todosProdutos);
            console.log('Todos produtos:', todosProdutos);
        } catch (error) {
            console.error('Erro ao buscar produtos:', error);
        } finally {
            setLoading(false);
        }
    };

    const pesquisarPorCodigo = async (codigo) => {
        setPesquisarPor("CODIGO");
        setTermo(codigo);
        setCategoria("");
        setCategorias([]);
        setFazerPesquisa(true);
    }

    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            pesquisar();
        } else if (/^[a-zA-Z0-9]$/.test(event.key)) { // Se for letra ou nmero
            setCategoria("");
            setCategorias([]);
        }
    };

    const selecionouEndereco = (endereco) => {
        endereco.geohash = ngeohash.encode(endereco.latitude, endereco.longitude, 12);
        console.log('endereco:', endereco);
        setEndereco(endereco);
        setModalVisivel(false);
    }

    return (<>
        <nav className="navbar bg-dark bg-gradient mb-3" data-bs-theme="dark">
            <div className="container-fluid">
                <span className="navbar-brand mx-auto">Busca de Produtos</span>
            </div>
        </nav>

        <div className="container-fluid">

            {/* Filtros */}
            <div className="row align-items-end justify-content-center mb-1">
                <div className="col-md-auto">
                    <label className='form-label'>Perodo:</label>
                    <select
                        className="form-select"
                        value={periodo}
                        onChange={(e) => setPeriodo(e.target.value)}
                    >
                        <option value="6">ltima hora</option>
                        <option value="5">ltimas 6 horas</option>
                        <option value="4">ltimas 12 horas</option>
                        <option value="3">ltimas 24 horas</option>
                        <option value="2">ltimos 7 dias</option>
                        <option value="1">ltimos 15 dias</option>
                        <option value="-1">ltimos 2 meses</option>
                    </select>
                </div>
                <div className="col-md-auto">
                    <label className='form-label'>Ordenao:</label>
                    <select
                        className="form-select"
                        value={ordem}
                        onChange={(e) => setOrdem(e.target.value)}
                    >
                        <option value="0">Menor preo</option>
                        <option value="1">Distncia</option>
                        <option value="2">Data da venda</option>
                        <option value="3">Maior preo</option>
                    </select>
                </div>
                <div className="col-md-auto">
                    <label className='form-label'>Pesquisar por:</label>
                    <select
                        className="form-select"
                        value={pesquisarPor}
                        onChange={(e) => setPesquisarPor(e.target.value)}
                    >
                        <option value="NOME">Nome</option>
                        <option value="CODIGO">Cdigo</option>
                    </select>
                </div>
                <div className="col-md-3">
                    <label htmlFor="produto" className='form-label'>Produto:</label>
                    <input
                        id="produto"
                        type="text"
                        value={termo}
                        onChange={(e) => setTermo(e.target.value)}
                        onKeyDown={handleKeyPress}
                        placeholder="Digite o nome do produto"
                        className="form-control"
                        ref={refTermo}
                    />
                </div>
                {pesquisarPor === "NOME" && categorias.length > 0 &&
                    <div className="col-md-2">
                        <label className='form-label'>Categoria:</label>
                        <select
                            className="form-select"
                            value={categoria}
                            onChange={(e) => setCategoria(e.target.value)}
                        >
                            {categorias.map((categ, index) => (
                                <option key={index} value={categ.id}>{categ.desc}</option>
                            ))}
                        </select>
                    </div>
                }
                <div className="col-md-2">
                    <label className="form-label">Distncia: {distancia} km</label>
                    <input type="range" className="form-range" min="1" max="20" step="1" value={distancia} onChange={(e) => setDistancia(e.target.value)} />
                </div>
            </div>
            <div className="row align-items-end justify-content-center mb-3">
                <div className="col-md-1 mt-2">
                    <button onClick={pesquisar} className="btn btn-primary">Buscar</button>
                </div>
            </div>
            {/* .Filtros */}

            {/* Lista de produtos */}
            <div className="container">
                {loading ? (
                    <p className="text-center text-secondary">Carregando...</p>
                ) : produtos.length > 0 ? (
                    <>
                        {produtos.map((produto, index) => (
                            <CardProduto key={index} idx={index + 1} produto={produto} pesquisarPorCodigo={pesquisarPorCodigo} />
                        ))}
                    </>
                ) : (
                    <p className='text-center text-secondary'>Nenhum produto encontrado</p>
                )}
            </div>
            {/* .Lista de produtos */}

            {/* Links */}
            <div className="row justify-content-center my-5">
                <div className="col-md-3">
                    <div className="list-group">
                        {endereco &&
                            <button
                                className="list-group-item list-group-item-action"
                                onClick={() => setModalVisivel(true)}
                            >
                                {endereco.nome}
                            </button>
                        }
                        <a className="list-group-item list-group-item-action" href="https://menorpreco.notaparana.pr.gov.br/" target="_blank" rel="noreferrer">Menor Preo</a>
                        <a className="list-group-item list-group-item-action" href="https://compras.menorpreco.pr.gov.br" target="_blank" rel="noreferrer">Compras Menor Preo</a>
                    </div>
                </div>
            </div>
            {/* .Links */}
        </div>

        <ModalEndereco
            visivel={modalVisivel}
            fecharModal={() => setModalVisivel(false)}
            selecionouEndereco={selecionouEndereco}
            podeFechar={!!endereco}
        />
    </>);
}

export default App;
