import Modal from "react-bootstrap/Modal";
import { Geocoder } from '@mapbox/search-js-react';

const MAPBOX_ACCESS_TOKEN = process.env.REACT_APP_MAPBOX_ACCESS_TOKEN;

const ModalEndereco = ({ visivel, fecharModal, selecionouEndereco, podeFechar }) => {

    return (
        <Modal
            show={visivel}
            onHide={fecharModal}
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header>
                <Modal.Title>Qual o seu endereo?</Modal.Title>
            </Modal.Header>
            <Modal.Body>

                <div className="alert alert-primary mb-3">
                    Insira um endereo completo, incluindo rua e nmero, para obter resultados mais precisos.
                </div>

                <Geocoder
                    accessToken={MAPBOX_ACCESS_TOKEN}
                    options={{
                        language: 'pt',
                        country: 'BR'
                    }}
                    onRetrieve={(result) => selecionouEndereco({
                        nome: result.properties.name,
                        latitude: result.properties.coordinates.latitude,
                        longitude: result.properties.coordinates.longitude
                    })}
                />

            </Modal.Body>
            {podeFechar &&
                <Modal.Footer>
                    <button className="btn btn-secondary" onClick={() => fecharModal()}>Cancelar</button>
                </Modal.Footer>
            }
        </Modal>
    );
};

export default ModalEndereco;
