import { format } from 'date-fns';

import './CardProduto.css';

const CardProduto = ({ idx, produto, pesquisarPorCodigo }) => {
    const dataHoraFormatada = format(new Date(produto.datahora), 'dd/MM/yyyy HH:mm');

    return (
        <div className={`card card-destaque mb-3 ${idx % 2 !== 0 ? 'bg-body-secondary' : ''}`}>
            <div className="card-body">
                <h5 className="card-title">
                    {`#${idx} - ${produto.desc}`}
                </h5>
                <h6 className="card-subtitle mb-2 text-body-secondary">
                    {produto.tempo}
                    <span className="fw-light" style={{ marginLeft: 10, fontSize: 12 }}>
                        ({dataHoraFormatada})
                    </span>
                </h6>
                <div className="row">
                    <div className="col">
                        <span className="fw-bold">Valor: </span>
                        R$ {produto.valor}
                        <span className="fw-light" style={{ marginLeft: 10, fontSize: 12 }}>
                            {`(${produto.valor_tabela} - ${produto.valor_desconto})`}
                        </span>
                    </div>
                    <div className="col">
                        <span className="fw-bold">Distncia: </span>
                        {produto.distkm} km
                    </div>
                </div>
                <div className="row">
                    <div className="col">
                        <span className="fw-bold">Estabelecimento: </span>
                        {`${produto.estabelecimento.nm_fan}${produto.estabelecimento.nm_fan && produto.estabelecimento.nm_emp ? ' - ' : ''}${produto.estabelecimento.nm_emp}`}
                    </div>
                    <div className="col">
                        <span className="fw-bold">Endereo: </span>
                        {`${produto.estabelecimento.nr_logr}, ${produto.estabelecimento.tp_logr} ${produto.estabelecimento.nm_logr}, ${produto.estabelecimento.bairro}${produto.estabelecimento.complemento ? ' - ' + produto.estabelecimento.complemento : ''}`}
                    </div>
                </div>
            </div>
            <div className="card-footer bg-transparent">
                <a className="btn btn-secondary" href={`https://www.google.com/maps/search/?api=1&query=${produto.estabelecimento.nr_logr}, ${produto.estabelecimento.tp_logr} ${produto.estabelecimento.nm_logr}`} target="_blank" rel="noreferrer">
                    Mapa
                </a>
                <button className="btn btn-secondary ms-3" onClick={() => pesquisarPorCodigo(produto.gtin)}>
                    Pesquisar por cdigo
                </button>
            </div>
        </div>
    );
}

export default CardProduto;